}

.contact-us_content-form-check {
    align-self: flex-start;
    position: relative;
    display: flex;
    cursor: pointer;
}

.contact-us_content-form-check div {
    width: 16px;
    margin-right: 23px;
}

.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    width: 16px;
    height: 16px;
    background-color: transparent;
    border: 1px solid #BF883F;
    cursor: pointer;
}

/* On mouse-over, add a grey background color */
.contact-us_content-form-check:hover input ~ .checkmark {
    background-color: #BF883F;
}

/* When the radio button is checked, add a blue background */
.contact-us_content-form-check input:checked ~ .checkmark {
    background-color: transparent;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the indicator (dot/circle) when checked */
.contact-us_content-form-check input:checked ~ .checkmark:after {
    display: block;
}

/* Style the indicator (dot/circle) */
.contact-us_content-form-check .checkmark:after {
    left: 6px;
    top: -6px;
    width: 7px;
    height: 15px;
    border: solid #bf883f;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

.contact-us_content-form-check input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.contact-us_content-form-check span {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 11px;
    line-height: 100%;
