.socialLinks {
  display: flex;

  .link {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    background: #FFF;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;

    &:not(:first-child) {
      margin-left: 32px;
    }

    svg {
      transform: scale(0.9);

      path {
        fill: #252525;
      }
    }
  }

}

.langs {
  align-self: flex-start;
  margin-top: 24px;
  display: flex;


  .btn {
    border: none;
    background: none;
    width: auto;
    height: auto;
    cursor: pointer;
    outline: none;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 13px;
    line-height: 16px;
    color: #FFFFFF;

    &:hover {
      box-shadow: none;
    }

    &:not(:first-child) {
      margin-left: 20px;
    }

    &_selected {
      color: #BF883F;
    }
  }
}