.langSwitcher {
  margin-left: 40px;
  display: flex;
  align-items: center;
  border-radius: 8px;
  background: #BF883F;
  position: relative;

  .btn {
    position: relative;
    z-index: 2;
    cursor: pointer;
    border: none;
    background: none;
    width: 30px;
    height: auto;
    padding: 5px 0;
    box-sizing: border-box;

    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      box-shadow: none;
    }

    p {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 400;
      font-size: 10px;
      line-height: 12px;
      color: #FFF;
      transition: 0.3s;
    }

    &_selected {
      p {
        color: #BF883F;
        font-weight: bold;
      }
    }
  }

  .mask {
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
    width: 30px;
    height: 100%;
    background: #FFF;
    border-radius: 8px;
    transition: 0.3s;

    &_eng {
      transform: translateX(100%);
    }

    &_ru {
      transform: translateX(200%);
    }
  }
}